  flex-direction: column;
  align-items: center;
}

.nav-horizontal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(180deg, rgba(18, 18, 18, 0.98), rgba(18, 18, 18, 0.95));
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
