  justify-content: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  box-shadow: 
    0 2px 20px rgba(0, 255, 0, 0.1),
    0 0 30px rgba(0, 255, 0, 0.05);
  animation: navSlideDown 0.5s ease-out, float 4s ease-in-out infinite;
  position: relative;
  overflow: hidden;
  height: 60px;
  align-items: center;
  max-width: 100%;
  box-sizing: border-box;
  overflow-x: auto; /* Allow horizontal scroll if needed */
  scrollbar-width: none; /* Hide scrollbar Firefox */
  -ms-overflow-style: none; /* Hide scrollbar IE/Edge */
  transition: all 0.3s ease;
}

.nav-horizontal::-webkit-scrollbar {
  display: none; /* Hide scrollbar Chrome/Safari/Webkit */
}

.nav-horizontal::before {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 150%;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--accent-color), transparent);
  animation: scanline 4s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  transform: skewX(-45deg);
}

.nav-horizontal::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: -50%;
  width: 50%;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--accent-color), transparent);
  animation: scanline 3s linear infinite reverse;
}

@keyframes scanline {
  0% {
    transform: translateX(-150%) skewX(-45deg);
  }
  100% {
    transform: translateX(150%) skewX(-45deg);
  }
}

@keyframes navSlideDown {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  50% {
    transform: translateY(-50%);
    opacity: 0.5;
  }
  70% {
    transform: translateY(-30%);
    opacity: 0.7;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.nav-horizontal .nav-links {
  display: flex;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  margin: 0;
  list-style: none;
  position: relative;
  flex-wrap: nowrap;
  max-width: 1200px;
  width: 100%;
  justify-content: space-around;
  padding: 0 1rem;
  min-width: min-content; /* Ensure items don't shrink too much */
}

.nav-horizontal .nav-links::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -20px;
  width: 10px;
  height: 10px;
  background: var(--accent-color);
  border-radius: 50%;
  transform: translateY(-50%);
  animation: pulse 2s infinite;
}

.nav-horizontal .nav-links li {
  margin: 0;
  position: relative;
}

.nav-horizontal .nav-links li::before {
  content: '[';
  position: absolute;
  left: -10px;
  color: var(--accent-color);
  opacity: 0;
  transition: all 0.3s ease;
}

.nav-horizontal .nav-links li::after {
  content: ']';
  position: absolute;
  right: -10px;
  color: var(--accent-color);
  opacity: 0;
  transition: all 0.3s ease;
}

.nav-horizontal .nav-links li:hover::before,
.nav-horizontal .nav-links li:hover::after {
  opacity: 1;
}

.nav-horizontal .nav-links li:not(:last-child)::after {
  content: '';
  position: absolute;
  top: 50%;
  right: -1rem;
  width: 0.5rem;
  height: 1px;
  background: var(--accent-color);
  opacity: 0.3;
}

.nav-horizontal .nav-links a {
