}

.loading-container {
  text-align: center;
  position: relative;
  max-width: min(600px, 95vw);
  width: 100%;
  padding: min(1.5rem, 3vh);
  max-height: 90vh;
  min-height: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  grid-gap: min(1rem, 2vh);
  gap: min(1rem, 2vh);
}

/* Microcontroller Board */
.mcu-board {
  margin-bottom: min(1.5rem, 3vh);
  display: flex;
  justify-content: center;
  flex-shrink: 0;
}

.board-outline {
  width: min(280px, 80vw);
  height: min(180px, 20vh);
  max-width: 280px;
  max-height: 180px;
  background: linear-gradient(135deg, #2a4a3a, #1a3a2a);
  border: 2px solid var(--accent-color);
  border-radius: 8px;
  position: relative;
  box-shadow: 
    0 0 20px rgba(0, 255, 157, 0.3),
    inset 0 0 30px rgba(0, 0, 0, 0.5);
  animation: boardPulse 3s ease-in-out infinite;
  box-sizing: border-box;
}

.mcu-chip {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80px;
  height: 60px;
  background: var(--chip-color);
  border: 1px solid var(--secondary-color);
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 15px rgba(0, 204, 126, 0.4);
}

.chip-label {
  font-size: 8px;
  color: var(--accent-color);
  font-weight: bold;
  margin-bottom: 4px;
  text-shadow: 0 0 5px var(--accent-color);
}

.chip-pins {
  display: flex;
  grid-gap: 2px;
  gap: 2px;
  flex-wrap: wrap;
  justify-content: center;
}

.pin {
  width: 6px;
  height: 2px;
  background: var(--pin-color);
  border-radius: 1px;
  animation: pinActivity 2s infinite;
}

.pin:nth-child(odd) {
  animation-delay: 0.5s;
}

/* Status LEDs */
.status-leds {
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  flex-direction: column;
  grid-gap: 8px;
  gap: 8px;
}

.led {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--led-off);
  transition: all 0.3s ease;
  border: 1px solid #444;
}

.led.on {
  box-shadow: 0 0 12px currentColor;
  border-color: currentColor;
}

.led.power.on {
  background: #ff3333;
  color: #ff3333;
}

.led.status.on {
  background: var(--warning-color);
  color: var(--warning-color);
}

.led.comm.on {
  background: var(--accent-color);
  color: var(--accent-color);
  animation: commBlink 1s infinite;
}

/* Components */
.components {
  position: absolute;
}

.capacitor {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 12px;
  height: 20px;
  background: linear-gradient(to right, #666 0%, #666 45%, transparent 45%, transparent 55%, #666 55%);
  border-radius: 2px;
}

.resistor {
  position: absolute;
  bottom: 30px;
  left: 30px;
  width: 20px;
  height: 6px;
  background: repeating-linear-gradient(
    90deg,
    #8B4513 0px,
    #8B4513 3px,
    #DAA520 3px,
    #DAA520 6px
  );
  border-radius: 3px;
}

.crystal {
  position: absolute;
  bottom: 20px;
  right: 30px;
  width: 16px;
  height: 8px;
  background: #C0C0C0;
  border: 1px solid #999;
  border-radius: 2px;
  position: relative;
}

.crystal::before,
.crystal::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 10px;
  background: #666;
  top: -1px;
}

.crystal::before {
  left: -2px;
}

.crystal::after {
  right: -2px;
}

/* GPIO Header */
.gpio-header {
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  grid-gap: 8px;
  gap: 8px;
}

.gpio-pin {
  width: 6px;
  height: 10px;
  background: var(--pin-color);
  border-radius: 1px 1px 0 0;
  animation: gpioActivity 3s infinite;
}

.gpio-pin:nth-child(even) {
  animation-delay: 1s;
}

/* Terminal Output */
.terminal-output {
  background: var(--terminal-bg);
  border: 1px solid var(--accent-color);
  border-radius: 8px;
  margin-bottom: min(1.5rem, 2vh);
  box-shadow: 0 0 20px rgba(0, 255, 157, 0.2);
  overflow: hidden;
  flex-shrink: 1;
  min-height: 0;
  width: 100%;
  box-sizing: border-box;
}

.terminal-header {
  background: linear-gradient(90deg, var(--chip-color), var(--terminal-bg));
  padding: min(8px, 1vh) min(12px, 2vw);
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--accent-color);
  flex-shrink: 0;
}

.terminal-title {
  font-size: clamp(0.6rem, 2vw, 0.8rem);
  color: var(--accent-color);
  font-weight: bold;
}

.terminal-controls {
  display: flex;
  grid-gap: 6px;
  gap: 6px;
}

.control {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  opacity: 0.7;
}

.control.minimize {
  background: var(--warning-color);
}

.control.maximize {
  background: var(--accent-color);
}

.control.close {
  background: var(--error-color);
}

.terminal-body {
  padding: min(12px, 2vw);
  height: clamp(80px, 15vh, 120px);
  max-height: 15vh;
  overflow-y: auto;
  font-size: clamp(0.6rem, 1.8vw, 0.75rem);
  line-height: 1.4;
  box-sizing: border-box;
}

.terminal-line {
  color: var(--text-color);
  margin-bottom: 4px;
  display: flex;
  justify-content: space-between;
  animation: terminalType 0.5s ease-out;
}

.terminal-line.current {
  color: var(--accent-color);
}

.terminal-line.current.clickable {
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 5px;
  border-radius: 3px;
}

.terminal-line.current.clickable:hover {
  background: rgba(0, 255, 157, 0.1);
  color: var(--warning-color);
  text-shadow: 0 0 10px var(--warning-color);
  transform: translateX(5px);
}

.status-ok {
  color: var(--accent-color);
  font-weight: bold;
}

/* System Status */
.system-status {
  display: flex;
  justify-content: space-around;
  margin-bottom: min(1.5rem, 2vh);
  padding: min(1rem, 2vw);
  background: var(--terminal-bg);
  border: 1px solid var(--secondary-color);
  border-radius: 6px;
  box-shadow: 0 0 15px rgba(0, 204, 126, 0.2);
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
}

.status-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 4px;
  gap: 4px;
  flex: 1 1;
}

.status-label {
  font-size: clamp(0.6rem, 1.5vw, 0.7rem);
  color: var(--secondary-color);
  font-weight: bold;
}

.status-value {
  font-size: clamp(0.7rem, 1.8vw, 0.8rem);
  color: var(--accent-color);
  text-shadow: 0 0 5px var(--accent-color);
  animation: valueFlicker 2s infinite;
}

/* Progress Container */
.progress-container {
  margin-bottom: min(2rem, 3vh);
  width: 100%;
  box-sizing: border-box;
  flex-shrink: 0;
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: min(0.5rem, 1vh);
  width: 100%;
}

.loading-label {
  color: var(--accent-color);
  font-size: clamp(0.7rem, 2vw, 0.9rem);
  font-weight: bold;
  text-shadow: 0 0 10px rgba(0, 255, 157, 0.5);
  animation: labelPulse 1.5s ease-in-out infinite;
}

.hex-value {
  color: var(--warning-color);
  font-size: clamp(0.6rem, 1.8vw, 0.8rem);
  font-family: 'Share Tech Mono', monospace;
  text-shadow: 0 0 8px var(--warning-color);
}

.progress-bar {
  width: 100%;
  height: clamp(8px, 2vh, 12px);
  background: var(--terminal-bg);
  box-sizing: border-box;
  border: 1px solid var(--accent-color);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
  box-shadow: 
    inset 0 2px 4px rgba(0, 0, 0, 0.4),
    0 0 15px rgba(0, 255, 157, 0.3);
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, 
    var(--secondary-color), 
    var(--accent-color), 
    var(--secondary-color)
  );
  transition: width 0.3s ease;
  position: relative;
  box-shadow: 0 0 15px rgba(0, 255, 157, 0.6);
  animation: progressGlow 2s ease-in-out infinite;
}

.progress-scanner {
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.6), transparent);
  animation: scannerMove 1.5s linear infinite;
}

.progress-text {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  color: var(--text-color);
  text-align: center;
}

/* Data Stream */
.data-stream {
  margin-bottom: min(1.5rem, 2vh);
  text-align: center;
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
}

.stream-line {
  display: flex;
  justify-content: center;
  grid-gap: min(1rem, 3vw);
  gap: min(1rem, 3vw);
  animation: streamFlow 2s ease-in-out infinite;
  flex-wrap: wrap;
}

.data-packet {
  background: var(--chip-color);
  color: var(--accent-color);
  padding: min(4px, 1vw) min(8px, 2vw);
  border-radius: 4px;
  font-size: clamp(0.65rem, 1.5vw, 0.75rem);
  border: 1px solid var(--accent-color);
  box-shadow: 0 0 8px rgba(0, 255, 157, 0.4);
  animation: packetPulse 1s infinite;
  white-space: nowrap;
}

.data-packet:nth-child(2) {
  animation-delay: 0.3s;
}

.data-packet:nth-child(3) {
  animation-delay: 0.6s;
}

/* Circuit Background */
.circuit-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0.3;
  z-index: -1;
}

.circuit-trace {
  position: absolute;
  background: var(--accent-color);
  box-shadow: 0 0 4px var(--accent-color);
}

.trace-1 {
  width: 100px;
  height: 1px;
  top: 20%;
  left: 10%;
  animation: traceFlow 3s linear infinite;
}

.trace-2 {
  width: 1px;
  height: 80px;
  top: 30%;
  right: 15%;
  animation: traceFlow 3s linear infinite 1s;
}

.trace-3 {
  width: 120px;
  height: 1px;
  bottom: 25%;
  left: 20%;
  animation: traceFlow 3s linear infinite 2s;
}

.circuit-via {
  position: absolute;
  width: 6px;
  height: 6px;
  background: var(--accent-color);
  border-radius: 50%;
  box-shadow: 0 0 8px var(--accent-color);
}

.via-1 {
  top: 20%;
  left: 20%;
  animation: viaPulse 2s infinite;
}

.via-2 {
  top: 60%;
  right: 25%;
  animation: viaPulse 2s infinite 0.7s;
}

.via-3 {
  bottom: 30%;
  left: 30%;
  animation: viaPulse 2s infinite 1.4s;
}

/* Blinking Cursor */
.blinking-cursor {
  display: inline-block;
  width: 8px;
  height: 14px;
  background-color: var(--accent-color);
  animation: blink 1s infinite;
  vertical-align: middle;
  margin-left: 4px;
  box-shadow: 0 0 5px var(--accent-color);
}

/* Animations */
@keyframes boardPulse {
  0%, 100% { 
    box-shadow: 0 0 20px rgba(0, 255, 157, 0.3), inset 0 0 30px rgba(0, 0, 0, 0.5);
  }
  50% { 
    box-shadow: 0 0 30px rgba(0, 255, 157, 0.5), inset 0 0 30px rgba(0, 0, 0, 0.5);
  }
}

@keyframes pinActivity {
  0%, 100% { background: var(--pin-color); }
  50% { background: var(--accent-color); box-shadow: 0 0 4px var(--accent-color); }
}

@keyframes commBlink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0.3; }
}

@keyframes gpioActivity {
  0%, 100% { background: var(--pin-color); }
  33% { background: var(--accent-color); box-shadow: 0 0 6px var(--accent-color); }
  66% { background: var(--warning-color); box-shadow: 0 0 6px var(--warning-color); }
}

@keyframes terminalType {
  from { opacity: 0; transform: translateX(-10px); }
  to { opacity: 1; transform: translateX(0); }
}

@keyframes valueFlicker {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

@keyframes labelPulse {
  0%, 100% { 
    text-shadow: 0 0 10px rgba(0, 255, 157, 0.5);
  }
  50% { 
    text-shadow: 0 0 20px rgba(0, 255, 157, 0.8);
  }
}

@keyframes progressGlow {
  0%, 100% { box-shadow: 0 0 15px rgba(0, 255, 157, 0.6); }
  50% { box-shadow: 0 0 25px rgba(0, 255, 157, 0.9); }
}

@keyframes scannerMove {
  0% { transform: translateX(-20px); opacity: 0; }
  50% { opacity: 1; }
  100% { transform: translateX(20px); opacity: 0; }
}

@keyframes streamFlow {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

@keyframes packetPulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 0 0 8px rgba(0, 255, 157, 0.4);
  }
  50% { 
    transform: scale(1.05);
    box-shadow: 0 0 12px rgba(0, 255, 157, 0.6);
  }
}

@keyframes traceFlow {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 1; }
}

@keyframes viaPulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 0 0 8px var(--accent-color);
  }
  50% { 
    transform: scale(1.3);
    box-shadow: 0 0 15px var(--accent-color);
  }
}

@keyframes blink {
  0%, 49% { opacity: 1; }  50%, 100% { opacity: 0; }
}

/* Start Button */
.start-button-container {
  margin: min(1.5rem, 2vh) 0;
  display: flex;
  justify-content: center;
  animation: buttonPulse 2s ease-in-out infinite;
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
}

.start-button {
  background: linear-gradient(135deg, var(--accent-color), var(--secondary-color));
  border: 2px solid var(--accent-color);
  border-radius: 8px;
  padding: min(12px, 2vw) min(24px, 4vw);
  color: var(--text-color);
  font-family: 'Share Tech Mono', monospace;
  font-size: clamp(0.8rem, 2.5vw, 1rem);
  font-weight: bold;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  grid-gap: min(8px, 1vw);
  gap: min(8px, 1vw);
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 
    0 0 20px rgba(0, 255, 157, 0.3),
    inset 0 0 20px rgba(0, 0, 0, 0.2);
  min-height: 44px;
  box-sizing: border-box;
  white-space: nowrap;
}

.start-button:hover {
  background: linear-gradient(135deg, var(--secondary-color), var(--accent-color));
  box-shadow: 
    0 0 30px rgba(0, 255, 157, 0.5),
    inset 0 0 30px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.start-button:active {
  transform: translateY(0);
  box-shadow: 
    0 0 15px rgba(0, 255, 157, 0.4),
    inset 0 0 25px rgba(0, 0, 0, 0.3);
}

.button-icon {
  font-size: 1.2rem;
  animation: iconSpark 1.5s ease-in-out infinite;
}

.button-text {
  position: relative;
  z-index: 2;
}

.button-underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--warning-color);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.start-button:hover .button-underline {
  transform: scaleX(1);
}

.start-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.start-button:hover::before {
  left: 100%;
}

@keyframes buttonPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes iconSpark {
  0%, 100% { color: var(--accent-color); text-shadow: 0 0 5px var(--accent-color); }
  50% { color: var(--warning-color); text-shadow: 0 0 10px var(--warning-color); }
}

/* Dashboard Button - Enhanced styling */
.dashboard-button {
  background: linear-gradient(135deg, var(--secondary-color), var(--warning-color));
  border: 2px solid var(--warning-color);
  animation: dashboardPulse 1.5s ease-in-out infinite;
}

.dashboard-button:hover {
  background: linear-gradient(135deg, var(--warning-color), var(--secondary-color));
  box-shadow: 
    0 0 40px rgba(255, 193, 7, 0.6),
    inset 0 0 30px rgba(0, 0, 0, 0.1);
  transform: translateY(-3px) scale(1.02);
}

.dashboard-button .button-icon {
  animation: rocketPulse 1s ease-in-out infinite;
}

@keyframes dashboardPulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 0 0 20px rgba(255, 193, 7, 0.4);
  }
  50% { 
    transform: scale(1.03);
    box-shadow: 0 0 30px rgba(255, 193, 7, 0.6);
  }
}

@keyframes rocketPulse {
  0%, 100% { 
    color: var(--warning-color); 
    text-shadow: 0 0 5px var(--warning-color);
    transform: translateY(0);
  }
  50% { 
    color: var(--accent-color); 
    text-shadow: 0 0 15px var(--accent-color);
    transform: translateY(-2px);
  }
}

/* Transition Effects */
.loading-page.transitioning {
  animation: systemShutdown 2.5s ease-in-out forwards;
}

.loading-container.transitioning {
  animation: containerFadeOut 2.5s ease-in-out forwards;
}

.board-outline.transitioning {
  animation: boardPowerDown 2s ease-in-out forwards;
}

.terminal-output.transitioning {
  animation: terminalClose 1.5s ease-in-out forwards;
}

@keyframes systemShutdown {
  0% { 
    background: var(--bg-color);
    opacity: 1;
  }
  70% {
    background: radial-gradient(circle, var(--accent-color), var(--bg-color));
    opacity: 0.8;
  }
  100% { 
    background: var(--accent-color);
    opacity: 0;
    transform: scale(1.1);
  }
}

@keyframes containerFadeOut {
  0% { 
    opacity: 1;
    transform: scale(1) rotateY(0deg);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.95) rotateY(5deg);
  }
  100% { 
    opacity: 0;
    transform: scale(0.8) rotateY(15deg);
  }
}

@keyframes boardPowerDown {
  0% { 
    box-shadow: 0 0 20px rgba(0, 255, 157, 0.3);
    opacity: 1;
  }
  50% {
    box-shadow: 0 0 40px rgba(0, 255, 157, 0.8);
    opacity: 0.7;
  }
  100% { 
    box-shadow: 0 0 5px rgba(0, 255, 157, 0.1);
    opacity: 0.3;
    transform: scale(0.9);
  }
}

@keyframes terminalClose {
  0% { 
    opacity: 1;
    transform: scaleY(1);
  }
  50% {
    opacity: 0.6;
    transform: scaleY(0.8);
  }
  100% { 
    opacity: 0;
    transform: scaleY(0);
  }
}

/* Boot Complete State */
.boot-complete .status-leds .led {
  animation: ledCelebration 0.5s ease-in-out infinite alternate;
}

.boot-complete .progress-fill {
  background: linear-gradient(90deg, var(--accent-color), var(--warning-color));
  animation: progressCelebration 2s ease-in-out infinite;
}

@keyframes ledCelebration {
  0% { box-shadow: 0 0 12px currentColor; }
  100% { box-shadow: 0 0 20px currentColor, 0 0 30px currentColor; }
}

@keyframes progressCelebration {
  0%, 100% { filter: brightness(1); }
  50% { filter: brightness(1.3); }
}

/* Responsive Design */

/* Large desktop screens */
@media (min-width: 1400px) {
  .loading-container {
    max-width: 700px;
  }
  
  .board-outline {
    width: 320px;
    height: 200px;
  }
  
  .mcu-chip {
    width: 90px;
    height: 65px;
  }
  
  .terminal-body {
    height: 140px;
    font-size: 0.85rem;
  }
}

/* Desktop screens */
@media (min-width: 1024px) and (max-width: 1399px) {
  .loading-container {
    max-width: 650px;
  }
  
  .board-outline {
    width: 300px;
    height: 190px;
  }
}

/* Tablet landscape */
@media (min-width: 769px) and (max-width: 1023px) {
  .loading-container {
    width: 90%;
    max-width: 550px;
    padding: 1.25rem;
  }

  .board-outline {
    width: 260px;
    height: 165px;
  }

  .mcu-chip {
    width: 70px;
    height: 50px;
  }

  .terminal-body {
    height: 100px;
    font-size: 0.75rem;
  }

  .system-status {
    grid-gap: 1rem;
    gap: 1rem;
  }
}

/* Tablet portrait and small laptops */
@media (max-width: 768px) {
  .loading-container {
    width: 95vw;
    padding: min(1rem, 2vh);
    max-height: 95vh;
    min-height: 0;
