.robot-project {
  background-color: var(--terminal-bg);
  border-radius: 10px;
  border: 1px solid var(--robot-primary);
  transition: all 0.3s;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  position: relative;
  transform: translateY(50px);
  opacity: 0;
  min-height: 300px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
}

.robot-project.visible {
  transform: translateY(0);
  opacity: 1;
}

.robot-project:hover {
  transform: scale(1.03);
  box-shadow: 0 15px 30px rgba(0, 136, 255, 0.3);
}

.project-image {
  width: 100%;
  height: 200px;
  margin-bottom: 1rem;
  border-radius: 8px;
  overflow: hidden;
}

.project-image .carousel-container {
  height: 100%;
}

.project-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  text-align: center;
}

.project-title {
  font-size: 1.3rem;
  color: var(--robot-primary);
  font-weight: bold;
  margin: 0;
}

.project-links {
  display: flex;
  gap: 1rem;
  margin-top: auto;
}

.project-link {
  padding: 0.6rem 1.2rem;
  background: var(--chip-color);
  color: var(--robot-primary);
  text-decoration: none;
  border-radius: 5px;
  border: 1px solid var(--robot-primary);
  transition: all 0.3s;
  font-weight: bold;
  cursor: pointer;
}

.project-link:hover {
  background: var(--robot-primary);
  color: var(--terminal-bg);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .robot-project {
    padding: 1rem;
    min-height: 250px;
  }

  .project-image {
    height: 150px;
  }

  .carousel-button {
    width: 30px;
    height: 30px;
    font-size: 1.5rem;
  }

  .carousel-dots {
    bottom: 0.5rem;
  }

  .carousel-dot {
    width: 8px;
    height: 8px;
  }
}